

.backgroundimage {
    width: 100%;
    min-height: 100vh;
    filter: blur(2px);
    transition: 1s;
}

.imagecontainer {
    position: absolute;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    top: 0;
    left: 0;
    z-index: -1;
    background-color: #111133;
    object-fit: cover;
}